.wishes-section {
	position: relative;

	.wishes-form-wrapper {
		position: relative;
		padding: 5rem 0 2rem;

		@include max-sm {
			padding: 3rem 0 0;
		}

		&::before {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 50%;
			background-color: var(--color-secondary);
			opacity: .1;
			box-shadow: 0 -1rem 1rem var(--color-secondary);
			content: "";
		}

		.orn-1 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 25%;
			transform: translate(-40%, 0%);

			@include max-lg {
				width: 50%;
			}

			@include max-sm {
				width: 65%;
				transform: translate(-40%, 20%);
			}
		}
	}

	.card-form {
		background-color: var(--color-primary);
		color: var(--color-dark);
		border-radius: var(--border-radius);
		box-shadow: 0 0 1rem rgba($color: #000000, $alpha: .15);

		&.card {
			border: none;
		}

		.card-body {
			padding: 3rem 2rem;

			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		padding: 2rem 0 5rem;
		z-index: 3;

		&::before {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: var(--color-secondary);
			opacity: .1;
			box-shadow: 0 1rem 1rem var(--color-secondary);
			content: "";
		}

		@include max-sm {
			padding: 0 0 3rem;
		}

		.card {
			position: relative;
			border: none;
			background-color: transparent;

			.card-body {
				overflow: hidden;
				padding: 2rem 0;

				.wishes {
					text-align: left;
					max-height: 32rem;
					overflow: auto;
					padding-right: 1rem;
					color: var(--color-dark);

					&::-webkit-scrollbar {
						width: 5px;
					}

					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}

					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}

					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;

						h6 {
							font-weight: bold;
						}

						h6,
						p {
							margin-bottom: 0;
						}

						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}

						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 0.8);
							border-radius: 5px;
							position: relative;

							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 0.8);
							}
						}
					}
				}
			}
		}

		.orn-2 {
			position: absolute;
			top: 50%;
			right: 0;
			width: 30%;
			width: 25%;
			transform: translate(40%, -50%) scaleX(-1);

			@include max-lg {
				width: 50%;
			}

			@include max-sm {
				width: 65%;
				transform: translate(50%, -50%) scaleX(-1);
			}
		}
	}
}